}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */

.wrapper {
 display: flex;
 width: 100%;
 align-items: stretch;
}

#sidebar {
 min-width: 250px;
 max-width: 250px;
 background: #fff;
 color: rgb(11, 152, 140);
 transition: all 0.3s;
}

#sidebar.active {
 margin-left: -250px;
 background-color: #fff;
}

#sidebar .sidebar-header {
 padding: 20px;
 background: #6d7fcc;
}

#sidebar ul.components {
 padding: 20px 0;
 border-bottom: 1px solid #47748b;
 background-color: #fff;
}

#sidebar ul h3 {
 color: rgb(29, 8, 78);
 padding: 10px;
}

#sidebar ul li a {
 padding: 10px;
 font-size: 1.1em;
 display: block;
}

#sidebar ul li a:hover {
 color: darkred;
 background: rgb(244, 231, 231);
 margin-left: 1rem;
}
#sidebar ul li a.active{
color: darkred;
margin-left: 1rem;
border-left: 2px solid darkred;
}
ul h5{
    -webkit-text-decoration: underline rgb(151, 5, 5);
            text-decoration: underline rgb(151, 5, 5);
    margin-left: 2rem;
}
.list-unstyled{
 margin-top: 1.5rem;
}
#sidebar ul li.active>a,
a[aria-expanded="true"] {
 color: #fff;
 background: #6d7fcc;
}

a[data-toggle="collapse"] {
 position: relative;
}

.dropdown-toggle::after {
 display: block;
 position: absolute;
 top: 50%;
 right: 20px;
 -webkit-transform: translateY(-50%);
         transform: translateY(-50%);
}

ul ul a {
 font-size: 0.9em !important;
 padding-left: 30px !important;
 background: #6d7fcc;
}

