body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f3f4f6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.navbar-brand {
  color: rgb(0, 145, 255);
  font-size: 2rem;
  font-weight: 600;
}

.navbar-brand span{
  color: rgb(8, 8, 83);
  font-size: 2rem;
  font-weight: 600;
}

.navbar-brand img {
  width: 30px;
  margin-right: 10px;
}

.btn {
  font-size: 15px;
}

.btn-primary,
.bg-primary {
  background-color: #df3ca6 !important;
  border: none;
}

.btn-primary:hover {
  background-color: #df3ca6;
  opacity: 0.9;
}

.btn-secondary {
  background-color: #7430f9;
}

.btn-secondary:hover {
  background-color: #7430f9;
  opacity: 0.9;
}

.icon {
  margin-right: 5px;
}

.menu{
  margin: 0 10px;
  font-size: 2.5rem;
  color: rgb(0, 145, 255);
  outline: none;
  border: none;
}

.nav-container{
  display: flex;
}
.sidebar{
  position: fixed;
  top: 0;
  left: 0;
  height: auto;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  margin-right: 3rem;
}
.nav-links{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.auth-wrapper{
  position: absolute;
  top: 0%;
  left: 0;
  z-index: 15;
  width: 100%;
  height: 100vh;
  background-color: white;
}
.switch-mode{
  outline: none !important;
  border: none;
  font-size: 1.8rem;
  color: rgb(47, 131, 195);
  margin: 1rem 0 0 1rem;
}

.profile-nav{
  width: 10rem;
  height: 10rem;
  margin: 20px auto;
}
.profile-text {
  width: 10rem;
  height: 10rem;
  margin: 20px auto;
}
.img-task{
  width: 50vw;
  height: 60vh;
}
.stat{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  row-gap: 2rem;
  column-gap: 4rem;
}

.chart{
  display: flex;
  flex-wrap: wrap;
  box-shadow: 5px 5px 10px black;
}
/* -------Pagination --------------- */
.paginationButtons {
  margin: 40px auto 30px 100px;
  width: 80%;
  height: 40px;
  list-style: none;
  display: flex;
  justify-content: center;
  }
        
                .paginationButtons a {
                  padding: 10px;
                  margin: 10px 10px;
                  border-radius: 5px;
                  border: 1px solid blue;
                  color: blue;
                  cursor: pointer;
                }
        
                .paginationButtons a:hover {
                  background-color: blue;
                  color: white !important;
                }
        
                .active a {
                  background-color: blue;
                  color: white !important;
                }
        
                @media screen and (max-width: 1560px) {
                  .paginationButtons {
                    margin: 30px auto 30px auto;
                    justify-content: center;
                    flex-wrap: wrap;
                  }
                }

        /* --------------------------------------- */





@media only screen and (max-width: 600px) {
  .sidebar{
    width: 100%;
    margin-bottom: 2rem;
  }
  .nav-links{
    justify-content: center;
    gap: 3rem;
  }
  .stat{
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}