@import "https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700";

/* body {
 font-family: 'Poppins', sans-serif;
 background: #fafafa;
} */

p {
 font-family: 'Poppins', sans-serif;
 font-size: 1.1em;
 font-weight: 300;
 line-height: 1.7em;
 color: #999;
}

a,
a:hover,
a:focus {
 color: inherit;
 text-decoration: none;
 transition: all 0.3s;
}

.navbar {
 display: flex;
 flex-wrap: wrap;
 /* width: 85%; */
 padding: 15px 10px;
 background: #fff;
 border: none;
 border-radius: 0;
 margin-bottom: 40px;
 box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.1);
 z-index: 2;
}

.navbar-btn {
 box-shadow: none;
 outline: none !important;
 border: none;
}

.toggle-icon{
 font-size: 3rem;
 color: rgb(43, 124, 175);
 outline: none !important;
}
.collapse span{
 color: rgb(2, 2, 57);
 font-weight: 600;
 margin-left: 20px;
}
.collapse h2{
 color: rgb(7, 110, 142);
}

.profile{
    display: flex;
    gap: 1rem;
    margin-right: 1rem;
    align-items: center;
    justify-content: center;
}
.profile h4{
    color: #47748b;
    font-weight: 600;
    padding-top: 0.5rem;
}

.avatar-nav{
 width: 2.5rem;
 height: 2.5rem;
 margin: 0 10px;
}

.text-nav {
    height: 2.5rem;
    width: 2.5rem;
    text-align: center;
    /* padding-top: 7px; */
    font-size: 1.5rem;
}

.line {
 width: 100%;
 height: 1px;
 border-bottom: 1px dashed #ddd;
 margin: 40px 0;
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */

.wrapper {
 display: flex;
 width: 100%;
 align-items: stretch;
}

#sidebar {
 min-width: 250px;
 max-width: 250px;
 background: #fff;
 color: rgb(11, 152, 140);
 transition: all 0.3s;
}

#sidebar.active {
 margin-left: -250px;
 background-color: #fff;
}

#sidebar .sidebar-header {
 padding: 20px;
 background: #6d7fcc;
}

#sidebar ul.components {
 padding: 20px 0;
 border-bottom: 1px solid #47748b;
 background-color: #fff;
}

#sidebar ul h3 {
 color: rgb(29, 8, 78);
 padding: 10px;
}

#sidebar ul li a {
 padding: 10px;
 font-size: 1.1em;
 display: block;
}

#sidebar ul li a:hover {
 color: darkred;
 background: rgb(244, 231, 231);
 margin-left: 1rem;
}
#sidebar ul li a.active{
color: darkred;
margin-left: 1rem;
border-left: 2px solid darkred;
}
ul h5{
    text-decoration: underline rgb(151, 5, 5);
    margin-left: 2rem;
}
.list-unstyled{
 margin-top: 1.5rem;
}
#sidebar ul li.active>a,
a[aria-expanded="true"] {
 color: #fff;
 background: #6d7fcc;
}

a[data-toggle="collapse"] {
 position: relative;
}

.dropdown-toggle::after {
 display: block;
 position: absolute;
 top: 50%;
 right: 20px;
 transform: translateY(-50%);
}

ul ul a {
 font-size: 0.9em !important;
 padding-left: 30px !important;
 background: #6d7fcc;
}

