/* ---------------------------------------------------
    CONTENT STYLE
----------------------------------------------------- */

#content {
 width: 100%;
 padding: 20px;
 min-height: 100vh;
 transition: all 0.3s;
}

/* ---------------------------------------------------
    MEDIAQUERIES
----------------------------------------------------- */

@media (max-width: 768px) {
 #sidebar {
  margin-left: -250px;
  background: #fff;

 }
#sidebar ul li {
 color:rgb(7, 110, 142);
 background: #6d7fcc;
}
 #sidebar ul li a:hover {
     color: darkred !important;
     background: rgb(244, 231, 231) !important;
     margin-left: 1rem;
 }

 #sidebar ul li a.active {
     color: darkred;
     margin-left: 1rem;
     border-left: 2px solid darkred;
 }

 #sidebar.active {
  margin-left: 0;
 background: #fff;
 }

 #sidebarCollapse span {
  display: none;
 }
}