




 .home-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 15;
  background: #f0f4f8;
  box-sizing:border-box;
 }

 .content-container{
  position: fixed;
  top: 0%;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  z-index: 2;
  gap: 3rem;
  background: #f0f4f8;
  padding: 0 6rem;
 }

 .desc-container{
  width: 50%;
  min-width: 300px;
 }
 
 .logoDiv {
  display: flex;
  justify-content: left;
  align-items: center;
  flex-wrap: wrap;
  width: 100px;
  margin-top: 16px;
  padding-left: 32px;
  padding-right: 16px

 }

 
 .logo {
  color: #2cb1bc;

 }

 
 .logoSpan {
  color: #044e54;
  font-weight: '550'
 }

 


 .heroDiv {
  margin-top: 110px;
  padding-left: 32px;
  width: auto;
  min-width: 300px;
 }

 
 .hero {
  color: #2cb1bc;
 }

 

 .heroSpan {
  color: #044e54;
  font-weight: 600
 }

 

 .descriptionDiv {
  margin-top: 25px;
  padding-left: 32px;
  min-width: 300px
 }

 

 .description {
  color: #627d98
 }

 

 .action {
  margin-top: 23px;
  padding-left: 32px
 }

 

 /* button: {
  background: '#2cb1bc',
  fontSize: '18px'
 } */

 
 .imageContainer {
  display: relative;
  margin-top: 100px;
  padding-left: 32px;
  min-width: 300px;
  /* width: 40%; */
 }

 

 .image {
  width: 90%;
  display: block;
  border-radius: 5px;
  margin: auto;
  height: 500px;
  /* boxShadow: '5px 5px 5px #044e54' */
 }

 @media (max-width: 800px){
  .content-container{
   flex-direction: column;
   height: auto;
   width: 100%;
   align-items: center;
  }
  .heroDiv{
   width: 100%;
   margin: 1rem auto;
  }
  .action{
   margin-top: -2rem;
  }
  .imageContainer{
   margin-top: -1rem;
  }
  .image{
   width: 300px;
   height: 280px;
   margin-bottom: 1rem;

  }
  .descriptionDiv{
   width: 100%;
   padding: 1rem;
  }
 }


